<template>
  <nav class="navbar navbar-expand-lg navbar-light bg-light mb-5">
    <router-link to="/" class="navbar-brand text-white">Logo</router-link> 
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarNav">
      <ul class="navbar-nav ml-auto">
        <li class="nav-item">
          <router-link to="/" class="nav-link text-white">Home</router-link>
        </li>
        <li class="nav-item" v-if='!isLoggedIn'>
          <router-link to="/login" class="nav-link text-white">Login</router-link>
        </li>
        <li class="nav-item" v-if='!isLoggedIn'>
          <router-link to="/register" class="nav-link text-white">Register</router-link>
        </li>
        <li class="nav-item" v-if='isLoggedIn'>
          <router-link to="/profile" class="nav-link text-white">Profile</router-link>
        </li>
        <li class="nav-item" v-if='isLoggedIn'>
          <a to="/logout" class="nav-link text-white" @click.prevent="logoutUser">Log out</a>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>

import { mapGetters, mapActions} from 'vuex'

export default {
  computed: {
    ...mapGetters(['isLoggedIn'])
  },
  methods: {
    ...mapActions(['logout']),
    logoutUser() {
      this.logout();
    }
  }
}
</script>

<style scoped>
  .navbar {
    background-color: rgba(23, 162, 191) !important;
  }

  a {
    cursor: pointer;
  }
</style>